/***
*
* Plugin -- Dropzone
*
*/

/**
* Particular Iterations
*/
#attachments{
  padding:2em;
  margin-bottom:1em;
  .dropzone__upload{
    width:100%;
  }
  .dropzone__options{
    width:100%;
    display: flex;
    justify-content: center;
  }
  .dropzone__filetype{
    padding:20px 10px 0;
  }

  &-preview-container{
    .dz-remove{
      font-size:1.25rem;
      margin-top:8px;
      right:10px;
    }
    .dz-complete{
      .dz-remove{
        background-color: transparent;
        color:color("red","base");
      }
    }
  }
}

#featured-image {
  .dropzone__upload, .dropzone__options {
    padding: 5em 1.5em;
  }
  .dropzone__upload {
    &:after {
      background: url('/vendor/42connect/coral/img/line-sep.png');
      background-repeat: no-repeat;
      height: 273px;
      width: 43px;
      content: '';
      position: absolute;
      right: 0;
      top: 0;
      bottom: 0;
      margin: auto;
    }
  }
  .dropzone__filetype{
    padding:0.5em 0;
    &:first-child{
      padding-top:0;
    }
    &:last-child{
      padding-bottom:0;
    }
  }
  .dz-remove{
    font-size:40px;
  }
  .dz-complete{
    .dz-remove{
      padding:10px 15px;
    }
  }
}

.dropzone{
  border: 3px dashed color("gray","lighten-1");
  background: color("gray","lighten-4");
  @extend %clearfix;
  &:hover{
    background: color("shade","white");
  }
  min-height:300px;
}
.dropzone-clickable{
  cursor:pointer;
}
.dropzone__filetype{
  img{
    width: 50px;
  }
}
.dropzone__upload{
  position: relative;
  float:left;
  width:60%;
  text-align: center;
  min-height:1px;
}
.dropzone__icon{
  font-size:100px;
  color:color("gray","lighten-1");
  i{
    line-height:normal !important;
  }
}
.dropzone__title,.dropzone__button{
  color:color("blue","base");
}
.dropzone__title{
  font-size:2rem;
}
.dropzone__button{
  font-size:1.25rem;
  padding:1rem;
  border: 1px solid color("blue","base");
  max-width:300px;
  margin:10px auto;
  border-radius:5px;
}
.dropzone__options{
  float:left;
  width:40%;
}
.dz-upload{
  display: block;
  position: absolute;
  bottom: 0;
  background-color: color("green","base");
  height: 20px;
  max-height:20px;
  transition: width,max-height 0.5s linear;
}
.dz-preview{
  position: relative;
}

.dz-complete{
  .dz-remove{
    -webkit-animation:none;
    -o-animation:none;
    animation:none;
    color: color("shade","white");
    background-color: color("red","base");

    &:after{
      content: "\f1f8";
    }
  }
  .dz-upload{
    max-height:0;
  }
}
.dz-remove{
  position: absolute;
  top:0;
  right:0;
  -webkit-animation: fa-spin 2s infinite linear;
  animation: fa-spin 2s infinite linear;
  text-decoration: none;
  color: color("gray","lighten-2");

  &:after{
    content: "\f013";
    font-family: FontAwesome;
    font-style: normal;
    font-weight: normal;
  }
}
