.admin{
  a.btn, button{
    color: color("shade","white");
    background-color: color("green","base");
    border: $admin-border;

    &:hover{
      color: color("green","base");
      background-color: color("shade","white");
      border: $admin-border;
    }
  }

  .btn-group{
    display: inline-block;
    font-size: 0;
    @include clearfix;
    a.btn,button{
      &:first-child{
        border-right:0;
        border-left: 0;
      }
      position: relative;
      width: auto;
      display: inline-block;
    }
    & + a.btn, & + button, & + .btn-group{
      margin-left: 10px;
    }

    span{
      padding-left: 4px;
      font-size: 1rem;
    }
  }
}