table{
  width:100%;
  font-size:1rem;
  border-spacing: 0;
  @media #{$small-screen} {
    font-size:0.9rem;
  }
  tr{
    border: 0;
  }
  td,th{
    padding:5px;
    border: 0;
  }
  td,th{
    text-align: center;
  }
}

table.striped{
  tr{
    &:nth-child(even){
      background-color: color("gray","lighten-5");
    }
    &:nth-child(odd){
      background-color: color("shade","white");
    }
  }
  thead tr:first-child{
    background-color: transparent;
  }
}

table.grid{
  .clickable{
    &:hover{
      color: color("shade", "white");
      background-color: color("green","base");
      opacity: 0.9;
      cursor: pointer;
    }
  }

  .checkboxes{
    width:40px;
    @media #{$small-screen} {
      display: none;
    }
  }
  .id{
    width:40px;
    @media #{$small-screen} {
      display: none;
    }
  }
  .action{

  }
  .date{
    width:140px;
    @media #{$small-screen} {
      display: none;
    }
  }
}