.icon{
  &.bars{
    position: relative;
    padding-left: 1.25em;

    &:before{
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 25px;
      height: 4px;
      border-top: 10px double #FFF;
      border-bottom: 3px solid #FFF;
    }
  }
}

