.admin{

  .sidebar-left{
    width: $admin-sidebar-width;
    position: absolute;
    color: #FFF;

    @media #{$admin-sidebar-width-hidden} {
      width: 0;
      display: none;
    }

    a{
      display: block;
      -webkit-transition: all 0.3s; /* Safari */
      transition: all 0.3s;
      color: #FFF;
      text-decoration: none;

      &:hover{
        background: $admin-sidebar-hover-color
      }
    }

    .logo{
      height: $admin-nav-height;
      background-color: $admin-nav-background-color;
      text-align: center;
      border-right: 1px solid $admin-main-background-color;

      img{
        max-width: 100%;
        width: auto;
        padding:5px;
        max-height: $admin-nav-height;
      }
    }

    .section{
      .header{
        font-size: 11px;
        font-weight: normal;
        text-transform: uppercase;
        letter-spacing: 2px;
        padding:$admin-sidebar-element-padding;
        border-bottom: 1px solid $admin-sidebar-border-color;
      }
      .item{
        a{
          display: block;
          width:100%;
          padding:15px 20px;
          border-bottom: 1px solid $admin-sidebar-border-color;

          i{
            font-size: 14px;
            vertical-align: middle;
            margin-right: 10px;
            width: 16px;
            text-align: center;
          }
        }

        > a{
          &:after{
            content: "\f105";
            display: inline-block;
            font-family: "FontAwesome";
            padding-right: 20px;
            position: absolute;
            right: 0;
          }
          &.active{
            &:after{
              content: "\f107";
            }
          }
        }

        ul{
          padding: 0;
          margin: 0;
          list-style: none;
          display: none;

          li{
            a{
              padding-left: 49px;
            }
          }
        }
      }
    }

  }
}