.slab{
  background-color: $admin-slab-background-color;
  margin-bottom: $admin-margin;
  font-size: 14px;

  .slab-header,.slab-body,.slab-footer{
    padding: 15px;
  }
  .slab-header{
    border-bottom: $admin-border;

    &.with-button{
      padding: 15px;
      display: flex;
      align-items: center;
      justify-content: space-between;

      @media #{$admin-sidebar-width-hidden} {
        display: block;
      }
    }
  }
  .slab-body{

  }
  .slab-footer{
    border-top: $admin-border;
  }

  a.btn,button{
    font-size: 1rem;

    @media #{$admin-sidebar-width-hidden} {
      margin-top: 10px;
      font-size: 0.8rem;
    }
  }


  &.short{
    color: color("shade","white");
    font-size:2rem;

    .icon,.notes{
    }

    .icon{
      text-align: center;
      padding:50px 10px;
    }
    .notes{
      margin-top: 35px;
      padding-left: 25px;
      border-left:1px solid $admin-sidebar-border-color;
    }
  }
  i{
    line-height: 20px;
  }

  &--small{
    margin-bottom: 8px;
    .slab-body{
      padding:8px 15px;
    }
  }

  &--bordered{
    border:1px solid #CCCCCC;
  }
  &--success{
    border-left:5px solid color("green","base");
  }
  &--removeable{
    position: relative;
  }
  &__remove{
    display: block;
    position: absolute;
    right: 10px;
    top: 5px;
  }
}