.admin{

  header {
    nav{
      background-color: $admin-nav-background-color;
      height:$admin-nav-height;
      line-height: $admin-nav-height;
      width:100%;

      .item-set{
        float: left;
        
        &.right{
          float: right;
        }
      }
      ul{
        margin: 0;
        padding:0;
        list-style: none;

        li{
          float: left;
          a{
            padding:0 20px;
          }
        }
      }

      i{
        font-size: 1.2rem;
      }
    } 
  }
}