.admin{
  .success{
    border-left: 5px solid color("green","base");
    ul{
      list-style: none;
      padding-left: 0;
      li{
        color: #52990B;
      }
      i{
        vertical-align: middle;
        font-size: 38px;
        padding-right: 11px;
      }
    }
  }
  .errors{
    border-left: 5px solid $error-red;
    ul{
      list-style: none;
      padding-left: 0;
      li{
        color: $error-red;
      }
      i{
        vertical-align: middle;
        font-size: 38px;
        padding-right: 11px;
      }
    }
  }

}